function MenuSubcategory({text, displaySubcategory}) {
    return ( 
        <ul className={displaySubcategory ? "menusubcategory active" : "menusubcategory"}>
            {text.map(option => (
                <li key={option.text} className="menusubcategory__option">{option.text}</li>
            ))}
        </ul>
    );
}

export default MenuSubcategory;