import validationSchema from "./validation";
import { useFormik } from "formik";

import LogoContainer from "../components/LogoContainer";
import { Link } from "react-router-dom";

function RegisterForm() {
    const formik = useFormik({
        initialValues: {
            username: '',
            name: '',
            surname: '',
            email: '',
            password: '',
            confirmPassword: '',
        },
        validationSchema: validationSchema,
        onSubmit: (values, {resetForm}) => {
            resetForm()
        }
    })

    return ( 
        <>
        <LogoContainer />
        <h3 className="registerform__title">Za darmowe konto</h3>
        <form onSubmit={formik.handleSubmit} className="registerform">
            <label className="registerform__label">
                Nazwa uytkownika<span>*</span>
                <input className="registerform__input" type="text" name="username" value={formik.values.username} onChange={formik.handleChange}/>  
                {formik.errors.username && formik.touched.username && (<p className="registerform__error">{formik.errors.username}</p>)}
            </label>

            <label className="registerform__label">
                Twoje imi<span>*</span>
                <input className="registerform__input" type="text" name="name" value={formik.values.name} onChange={formik.handleChange}/>  
                {formik.errors.name && formik.touched.name && (<p className="registerform__error">{formik.errors.name}</p>)}
            </label>

            <label className="registerform__label">
                Twoje nazwisko
                <input className="registerform__input" type="text" name="surname" value={formik.values.surname} onChange={formik.handleChange}/>  
                {formik.errors.surname && formik.touched.surname && (<p className="registerform__error">{formik.errors.surname}</p>)}
            </label>

            <label className="registerform__label">
                Adres email<span>*</span>
                <input className="registerform__input" type="text" name="email" value={formik.values.email} onChange={formik.handleChange}/>  
                {formik.errors.email && formik.touched.email && (<p className="registerform__error">{formik.errors.email}</p>)}
            </label>

            <label className="registerform__label">
                Haso<span>*</span>
                <input className="registerform__input" type="password" name="password" value={formik.values.password} onChange={formik.handleChange}/>  
                {formik.errors.password && formik.touched.password && (<p className="registerform__error">{formik.errors.password}</p>)}
            </label>

            <label className="registerform__label">
                Powtrz haso<span>*</span>
                <input className="registerform__input" type="password" name="confirmPassword" value={formik.values.confirmPassword} onChange={formik.handleChange}/>  
                {formik.errors.confirmPassword && formik.touched.confirmPassword && (<p className="registerform__error">{formik.errors.confirmPassword}</p>)}
            </label>

            <button type="submit">Zarejestruj</button> 
        </form>

        <Link to='/'>Powrt do strony gwnej</Link>
        </>
     );
}

export default RegisterForm;